// базовые подключения
@font-face {
	font-family: 'Rosamarena';
	src: url(/fonts/rosamarena.woff2);
}

@import "vars";
@import "mixins";
// @import "fonts";
@import "settings";

body,
h2,
h3 {
  font-family: var(--font-primary);
  font-weight: var(--f-medium);
    color: var(--color-heading);
}

body{
font-size: 16px;
}

.site-container {
  background-color: var(--color-veronika-bg);
  // height: 100vh;
}
h3{

    // margin-bottom: 30px;
    font-size: 20px;
    color: var(--color-heading);
}

.text{

    font-size: 16px;
    font-weight: var(--f-regular);
    line-height: 1.45;
}

.cart-bottom{

    padding-bottom: 40px;
}


.container {
  padding-left: 40px;
  padding-right: 40px;
}

.flex-c {
  display: flex;
  align-items: center;
}
.flex{
  display: flex;
}
.flex-column{
  display: flex;
  flex-direction: column
}
// подключения компонентов страницы
@import "./components/header";
@import "./components/interesting";
@import "./components/about-me";
@import "./components/contacts";

