@font-face {
  font-family: "Rosamarena";
  src: url(/fonts/rosamarena.woff2);
}
:root {
  --font-family: "Open Sans", sans-serif;
  --content-width: 1170px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --color-primary: #6667ab;
  --color-secondary: #ac8188;
  --color-coral: #e9967a;
  --color-violet: purple;
  --color-pink: #db7093;
  --color-heading: #192335;
  --color-body: #6b7385;
  --color-white: #fff;
  --color-white-off: #ffffffab;
  --color-bodyest: #273041;
  --color-dark: #27272e;
  --color-darker: #192335;
  --color-black: #111113;
  --color-grey: hsla(0, 0%, 81%, 0.24);
  --color-grey2: #f4f5f7;
  --color-gray: #a1a9ac;
  --color-gray-light: #f6f6f6;
  --color-gray-lighter: #ebebeb;
  --color-light: #f5f7fa;
  --color-lighter: #f2f5f9;
  --primary-opacity: #2f57ef21;
  --secondary-opacity: #b966e721;
  --coral-opacity: #e9967a21;
  --violet-opacity: #80008021;
  --pink-opacity: #db709321;
  --white-opacity: #ffffff21;
  --danger-opacity: #ff000310;
  --warning-opacity: #ff8f3c10;
  --success-opacity: #3eb75e10;
  --black-opacity: rgba(0, 0, 0, 0.04);
  --heading-opacity: #19233550;
  --color-card-1: #fffccf;
  --color-card-2: #ffedff;
  --color-card-3: #ffe8eb;
  --color-card-4: #e9f6ff;
  --color-card-5: #5024d1;
  --color-card-6: #3b3b46;
  --color-success: #3eb75e;
  --color-danger: #ff0003;
  --color-warning: #ff8f3c;
  --color-info: #1ba2db;
  --color-primary-alt: #d1ebe6;
  --color-primary-light: #f1f7ff;
  --color-primary-lighter: #eef;
  --color-secondary-alt: #fef4f4;
  --color-extra: #f9f9ff;
  --color-extra2: #f9f9ff;
  --color-border: #e6e3f1;
  --color-border-2: #e6e3f14f;
  --color-badge-1: #303f5b;
  --color-badge-2: #f2c94c;
  --color-facebook: #3b5997;
  --color-twitter: #1ba1f2;
  --color-youtube: #ed4141;
  --color-linkedin: #0077b5;
  --color-pinterest: #e60022;
  --color-instagram: #c231a1;
  --color-vimeo: #00adef;
  --color-twitch: #6441a3;
  --color-discord: #7289da;
  --radius-10: 10px;
  --radius-big: 16px;
  --radius-small: 6px;
  --border-width: 2px;
  --radius: 6px;
  --f-light: 300;
  --f-regular: 400;
  --f-medium: 500;
  --f-semi-bold: 600;
  --f-bold: 700;
  --f-extra-bold: 800;
  --f-black: 900;
  --shadow-primary: 0px 4px 10px rgba(37, 47, 63, 0.1);
  --shadow-light: 0 2px 6px 0 rgba(0, 0, 0, 0.05);
  --shadow-dark: 0 2px 6px 0 rgba(0, 0, 0, 0.2);
  --shadow-1: 0px 6px 34px rgba(215, 216, 222, 0.41);
  --shadow-slide-item: 0px 0 10px 0 rgba(215, 216, 222, 0.2);
  --shadow-2: 0 15px 34px 0 rgba(175, 181, 204, 0.32);
  --shadow-3: 0 5px 15px 0 rgba(85, 74, 101, 0.37);
  --shadow-5: 0px 20px 70px 0px rgba(0, 0, 0, 0.1);
  --shadow-7: 0px 15px 30px -2px rgba(0, 0, 0, 0.1);
  --shadow-8: -5px 18px 60px 0 rgba(210, 210, 234, 0.4);
  --shadow-9: 0 13px 48px 0 rgba(215, 216, 222, 0.44);
  --shadow-10: 0 13px 14px 0 rgba(129, 104, 145, 0.05);
  --shadow-11: 0 0 10px 0 rgba(0, 0, 0, 0.06);
  --transition: 0.3s;
  --transition-2: 256ms ease-in-out;
  --transition-3: transform 0.85s cubic-bezier(0.23, 1, 0.32, 1),
    background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
  --transition-4: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
  --font-primary: "Montserrat";
  --font-secondary: "TenorSans";
  --font-special: "Rosamarena";
  --font-alt: "Source Serif Pro", serif;
  --font-size-b1: 24px;
  --font-size-b2: 18px;
  --font-size-b3: 16px;
  --font-size-b4: 12px;
  --line-height-b1: 1.5;
  --line-height-b2: 1.67;
  --line-height-b3: 1.67;
  --line-height-b4: 1;
  --h1: 52px;
  --h2: 44px;
  --h3: 34px;
  --h4: 30px;
  --h5: 20px;
  --h6: 16px;
  --div-gap-1: 10px;
  --gradient-1: linear-gradient(#fff, hsla(33, 26%, 92%, 0.9));
  --gradient-4: linear-gradient(180deg, #fff, #efeae4);
  --gradient-2: linear-gradient(0deg, #fff, #f0f2ff);
  --gradient-3: linear-gradient(90deg, #8da9fc, #b48dd5);
  --gradient-4: linear-gradient(180deg, #eff1ff, #fff);
  --gradient-5: linear-gradient(#eff1ff, #eff1ff);
  --gradient-6: linear-gradient(
    90deg,
    hsla(0, 0%, 100%, 0.15),
    rgba(251, 199, 226, 0.15),
    rgba(220, 217, 254, 0.15)
  );
  --gradient-7: linear-gradient(
    218.15deg,
    var(--color-secondary) 0%,
    var(--color-primary) 100%
  );
  --gradient-8: linear-gradient(270deg, #9e77ff, #6667ab);
  --gradient-9: linear-gradient(180deg, #6667ab, #e3f0ff);
  --color-veronika-bg: #efeae4;
  --color-special: #86999f;
}

