html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  font-size: inherit;
}

ul[class] {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
  border: none;
}

a {
  text-decoration: none;
}

fieldset {
  border: none;
  margin: 0;
  padding: 0;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

a {
  color: #fc0000;
}

input {
  border: 1px solid #43ff2d;
  color: #43ff2d;
  outline: none;
}

button {
  padding: 0;
  margin: 0;
  color: red;
  border: 1px solid green;
  border-radius: 0;
}

body,
h2,
h3 {
  font-family: var(--font-primary);
  font-weight: var(--f-medium);
  color: var(--color-heading);
}

body {
  font-size: 16px;
}

.site-container {
  background-color: var(--color-veronika-bg);
}

h3 {
  font-size: 20px;
  color: var(--color-heading);
}

.text {
  font-size: 16px;
  font-weight: var(--f-regular);
  line-height: 1.45;
}

.cart-bottom {
  padding-bottom: 40px;
}

.container {
  padding-left: 40px;
  padding-right: 40px;
}

.flex-c {
  display: flex;
  align-items: center;
}

.flex {
  display: flex;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.header {
  position: fixed;
  width: 100%;
}
.header .container {
  padding-left: 30px;
  padding-right: 30px;
  background-color: var(--color-veronika-bg);
  box-shadow: 0 20px 34px rgba(0, 0, 0, 0.0509803922);
  justify-content: space-between;
  color: var(--color-heading);
  z-index: 1;
}
.header .container:last-child .header__link {
  padding-top: 0;
}
@media (max-width: 500px) {
  .header .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (max-width: 380px) {
  .header .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}
.header .logo {
  width: 70px;
}
@media (max-width: 470px) {
  .header .logo {
    width: 50px;
  }
}
.header__logo {
  padding-top: 15px;
  padding-bottom: 15px;
  gap: 20px;
}
@media (max-width: 470px) {
  .header__logo {
    gap: 10px;
  }
}
.header__logo:hover .header__logo-subtitle, .header__logo:hover .header__logo-title {
  color: var(--color-primary) !important;
}
.header__logo-subtitle, .header__logo-title {
  color: var(--color-heading);
  transition: color 0.3s ease;
}
@media (max-width: 470px) {
  .header__logo-subtitle, .header__logo-title {
    font-size: 12px;
  }
}
@media (max-width: 340px) {
  .header__logo-subtitle, .header__logo-title {
    font-size: 10px;
  }
}
.header__logo-title {
  font-weight: var(--f-bold);
  margin-top: 5px;
}
.header__links {
  gap: 15px;
  margin: 0 auto;
}
@media (max-width: 1100px) {
  .header__links {
    gap: 0;
    position: fixed;
    z-index: 4;
    align-items: end;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    flex-direction: column;
    padding-top: 30px;
    background: rgba(0, 0, 0, 0.7);
    backdrop-filter: blur(3px);
    transform: translateX(300%);
    opacity: 0;
    transition: opacity 0.4s ease-out;
  }
  .header__links::before {
    content: "";
    position: absolute;
    width: 350px;
    height: 300vh;
    background-color: var(--color-white);
    top: -100px;
    z-index: -1;
    right: 0;
  }
}
.header .mobil-active {
  transform: translateX(0px);
  opacity: 1;
}
.header__link {
  color: var(--color-heading);
  transition: color 0.3s ease;
}
@media (min-width: 1101px) {
  .header__link {
    padding-top: 20px;
    padding-bottom: 20px;
    color: var(--color-heading);
    transition: color 0.3s ease;
  }
}
@media (max-width: 1100px) {
  .header__link {
    position: relative;
    padding-top: 15px;
    padding-bottom: 15px;
    width: 350px;
    padding-left: 60px;
    background-color: var(--color-white);
    z-index: 3;
  }
}
.header__link:hover {
  color: var(--color-primary);
}
.header .link-active {
  color: var(--color-primary);
  cursor: default;
}
.header__social-link {
  padding: 7px;
  display: flex;
  flex-direction: column;
}
@media (max-width: 380px) {
  .header__social-link {
    padding: 3px;
  }
}
.header__social-link svg path {
  fill: var(--color-heading);
  transition: fill 0.3s ease;
}
.header__social-link:hover path {
  fill: var(--color-primary);
}
.header .inst {
  padding-left: 11px;
}

.mobil-menu {
  width: 24px;
  height: 24px;
  position: relative;
  border: none;
  margin-left: 20px;
  cursor: pointer;
  z-index: 10;
  background: transparent;
}
@media (min-width: 1101px) {
  .mobil-menu {
    display: none;
  }
}
.mobil-menu::before, .mobil-menu::after {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 2px;
  background-color: var(--color-body);
}
.mobil-menu::before {
  top: 0;
  transition: all 0.4s ease;
}
.mobil-menu::after {
  top: 100%;
  transition: all 0.4s ease;
}
.mobil-menu span {
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  top: 50%;
  transform: translateY(-50%);
  background-color: var(--color-body);
  transition: opacity 0.4s ease;
}
@media (max-width: 380px) {
  .mobil-menu {
    margin-left: 15px;
  }
}

.mobil-menu-active span {
  opacity: 0;
}
.mobil-menu-active::after {
  top: 50%;
  transform: rotate(-45deg);
}
.mobil-menu-active::before {
  top: 50%;
  transform: rotate(45deg);
}

.interesting {
  padding-bottom: 30px;
}
.interesting__card {
  margin-bottom: 30px;
}
.interesting .noactive {
  width: 100%;
  background: var(--color-white);
  border-radius: var(--radius);
  box-shadow: var(--shadow-1);
  overflow: hidden;
  padding: 30px;
  cursor: pointer;
}
.interesting .noactive .interesting__card-text {
  opacity: 0;
  height: 0;
}
.interesting .noactive .interesting__card-text span {
  height: 0;
}
.interesting .noactive .podtext {
  opacity: 1;
  height: auto;
  text-indent: 30px;
}
.interesting__card-title {
  text-indent: 30px;
  color: var(--color-heading);
}
.interesting__card-text span {
  display: block;
  text-indent: 30px;
  padding-top: 16px;
}
.interesting__card-text-wrap {
  gap: 30px;
}
.interesting .li {
  line-height: 2;
}
.interesting .active {
  background: red;
}

.about-me__title {
  color: var(--color-violet);
  font-family: var(--font-special);
  font-size: 64px;
  font-weight: 400;
  margin-bottom: 20px;
  margin-top: 40px;
}
.about-me__text {
  margin-bottom: 10px;
}

.contacts__top-question {
  background: #ff4f25;
}
.contacts__top-consultation {
  background: var(--color-card-5);
}
.contacts__bottom {
  margin-top: 30px;
}
@media (max-width: 1180px) {
  .contacts__bottom {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: start;
  }
}
@media (max-width: 700px) {
  .contacts__bottom {
    grid-template-columns: repeat(1, 1fr);
  }
}
.contacts__bottom, .contacts__top {
  gap: 15px;
  justify-content: center;
}
.contacts__top-wats {
  background: #4dc85a;
}
.contacts__top-wats svg {
  transform: translateY(-1px);
}
.contacts__top-telegramm {
  background-color: #0084c6;
}
.contacts__top-vk {
  background: #145b87;
}

.contact-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  width: 300px;
  min-width: 14.25rem;
  color: #fff;
  font-size: 1rem;
  line-height: 1.5;
  font-weight: 500;
  text-align: center;
  padding: 0.75em 1.5em;
  border: none;
  border-radius: 2em;
  transition: all 0.25s ease-in-out;
  cursor: pointer;
}
.contact-btn svg {
  display: block;
  width: 16px;
  height: 16px;
}
@media (max-width: 1180px) {
  .contact-btn {
    margin: 0 auto;
  }
}
@media (max-width: 700px) {
  .contact-btn {
    width: 80%;
  }
}
@media (max-width: 400px) {
  .contact-btn {
    width: 100%;
    font-size: 14px;
  }
}