.interesting {
  padding-bottom: 30px;
  &__card{

  margin-bottom: 30px;
  }
  .noactive {
    width: 100%;
    // height: 120px;
    background: var(--color-white);
    border-radius: var(--radius);
    box-shadow: var(--shadow-1);
    overflow: hidden;
    padding: 30px;
    cursor: pointer;
    .interesting__card-text {
      opacity: 0;
      height: 0;
      span {
        height: 0;
      }
    }
    .podtext {
      opacity: 1;
      height: auto;
      text-indent: 30px;
    }
  }

  &__card-title {
    text-indent: 30px;
    color: var(--color-heading);
  }
  &__card-text {
    // font-size: 16px;
    // font-weight: var(--f-regular);
    // line-height: 1.45;
    span {
      display: block;
      text-indent: 30px;
      padding-top: 16px;
    }
  }
  &__card-text-wrap {
    // flex-direction: column;
    gap: 30px;
  }
  .li {
    line-height: 2;
  }
  .active{
    background: red;
  }
}
