.header {
  position: fixed;
  width: 100%;
  .container {
    padding-left: 30px;
    padding-right: 30px;
    background-color: var(--color-veronika-bg);
    box-shadow: 0 20px 34px #0000000d;
    justify-content: space-between;
    color: var(--color-heading);
    z-index: 1;
    &:last-child {
      .header__link {
        padding-top: 0;
      }
    }
    @media (max-width: 500px) {
      padding-left: 20px;
      padding-right: 20px;
    }
    @media (max-width: 380px) {
      padding-left: 10px;
      padding-right: 10px;
    }
  }
  .logo {
    width: 70px;
    @media (max-width: 470px) {
      width: 50px;
    }
  }

  &__logo {
    padding-top: 15px;
    padding-bottom: 15px;
    @media (max-width: 470px) {
      gap: 10px;
    }
    gap: 20px;
    &:hover .header__logo-subtitle,
    &:hover .header__logo-title {
      color: var(--color-primary) !important;
    }
  }

  &__logo-subtitle,
  &__logo-title {
    @media (max-width: 470px) {
      font-size: 12px;
    }
    @media (max-width: 340px) {
      font-size: 10px;
    }
    color: var(--color-heading);
    transition: color 0.3s ease;
  }

  &__logo-title {
    font-weight: var(--f-bold);
    margin-top: 5px;
  }
  &__links {
    gap: 15px;
    margin: 0 auto;
    @media (max-width: 1100px) {
      &::before {
        content: "";
        position: absolute;
        width: 350px;
        height: 300vh;
        background-color: var(--color-white);
        top: -100px;
        z-index: -1;
        right: 0;
      }
      gap: 0;
      position: fixed;
      z-index: 4;
      align-items: end;
      width: 100%;
      height: 100%;
      top: 0;
      right: 0;
      flex-direction: column;
      padding-top: 30px;
      background: rgba(0, 0, 0, 0.7);
      backdrop-filter: blur(3px);
      transform: translateX(+300%);
      opacity: 0;
      transition: opacity 0.4s ease-out;
    }
  }

  .mobil-active {
    transform: translateX(0px);
    opacity: 1;
  }

  &__link {
    @media (min-width: 1101px) {
      padding-top: 20px;
      padding-bottom: 20px;
      color: var(--color-heading);
      transition: color 0.3s ease;
    }
    @media (max-width: 1100px) {
      position: relative;
      padding-top: 15px;
      padding-bottom: 15px;
      width: 350px;
      padding-left: 60px;
      background-color: var(--color-white);
      z-index: 3;
    }
    color: var(--color-heading);
    transition: color 0.3s ease;
    &:hover {
      color: var(--color-primary);
    }
  }
  .link-active {
    color: var(--color-primary);
    cursor: default;
  }
  &__social-link {
    padding: 7px;
    display: flex;
    flex-direction: column;
    @media (max-width: 380px) {
      padding: 3px;
    }
    svg {
      path {
        fill: var(--color-heading);
        transition: fill 0.3s ease;
      }
    }
    &:hover {
      path {
        fill: var(--color-primary);
      }
    }
  }
  .inst {
    padding-left: 11px;
  }
}

.mobil-menu {
  @media (min-width: 1101px) {
    display: none;
  }
  width: 24px;
  height: 24px;
  position: relative;
  border: none;
  margin-left: 20px;
  cursor: pointer;
  z-index: 10;
  background: transparent;
  &::before,
  &::after {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 2px;
    background-color: var(--color-body);
  }
  &::before {
    top: 0;
    transition: all 0.4s ease;
  }
  &::after {
    top: 100%;
    transition: all 0.4s ease;
  }
  span {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    top: 50%;
    transform: translateY(-50%);
    background-color: var(--color-body);
    transition: opacity 0.4s ease;
  }
  @media (max-width: 380px) {
    margin-left: 15px;
  }
}

.mobil-menu-active {
  span {
    opacity: 0;
  }
  &::after {
    top: 50%;
    transform: rotate(-45deg);
  }
  &::before {
    top: 50%;
    transform: rotate(45deg);
  }
}
