.contacts {
  &__top-question {
    background: #ff4f25;
  }
  &__top-consultation {
    background: var(--color-card-5);
  }
  &__bottom {
    margin-top: 30px;
    @media (max-width: 1180px) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      align-items: start;
    }
    @media (max-width: 700px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &__bottom,
  &__top {
    gap: 15px;
    justify-content: center;
  }
  &__top-wats {
    background: #4dc85a;
    svg {
      transform: translateY(-1px);
    }
  }
  &__top-telegramm {
    background-color: #0084c6;
  }
  &__top-vk {
    background: #145b87;
  }
}
.contact-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  width: 300px;
  min-width: 14.25rem;
  color: #fff;
  font-size: 1rem;
  line-height: 1.5;
  font-weight: 500;
  text-align: center;
  padding: 0.75em 1.5em;
  border: none;
  border-radius: 2em;
  // background-color: #ff4f25;
  // box-shadow: 0 0 1.5em rgb(255 79 37 / 50%);
  transition: all 0.25s ease-in-out;
  cursor: pointer;
  svg {
    display: block;
    width: 16px;
    height: 16px;
  }
  @media (max-width: 1180px) {
    margin: 0 auto;
  }
  @media (max-width: 700px) {
    width: 80%;
  }
  @media (max-width: 400px) {
    width: 100%;
    font-size: 14px;
  }
}
